.home-container {
  /* 父级高度为flex: 1 子类达到100%? 父类设置( flex:1; position: relative; ), 子类设置( position: absolute; height: 100%; width: 100%;) */
  position: absolute;
  width: 100%;
  height: 80%;
}
.home-container > img {
  display: block;
  width: 120px;
  height: 120px;
  border-radius: 50%;
}
.home-container > h1 {
  margin: 12px 0;
  font-size: 30px;
  font-weight: 500;
}
.home-container > h3 {
  margin-bottom: 22px;
  color: rgba(0, 0, 0, 0.45);
  font-size: 18px;
  font-weight: 500;
}
.home-container > nav {
  width: 308px;
  display: flex;
  justify-content: space-between;
}
.home-container > nav:nth-of-type(1) {
  margin-bottom: 24px;
}
.home-container > nav:nth-of-type(2) {
  width: 210px;
}
.home-container > nav > button {
  width: 80px;
  height: 34px;
  border-radius: 34px;
  line-height: 34px;
  border: 1px solid #000;
  font-size: 16px;
  font-weight: 500;
}
.home-container > nav > button:hover {
  animation-name: hvr-pulse-grow;
  animation-duration: 0.75s;
  animation-timing-function: linear;
  animation-iteration-count: 1;
}
.home-container > nav > i {
  width: 30px;
  height: 30px;
  background-size: 100% 100%;
  background-repeat: no-repeat;
  cursor: pointer;
  box-shadow: 0 0 1px rgba(0, 0, 0, 0);
  -webkit-transition-duration: 0.3s;
  transition-duration: 0.3s;
  transition-property: transform;
}
.home-container > nav > i::before {
  pointer-events: none;
  position: absolute;
  z-index: -1;
  content: "";
  top: 100%;
  left: 5%;
  height: 10px;
  width: 90%;
  opacity: 0;
  background: radial-gradient(ellipse at center, rgba(0, 0, 0, 0.3) 0%, rgba(0, 0, 0, 0) 60%);
  transition-duration: 0.3s;
  transition-property: transform, opacity;
}
.home-container > nav > i:hover {
  transform: translateY(-5px);
}
.home-container > nav > i:hover::before {
  opacity: 1;
  transform: translateY(5px);
}
.home-container > nav > i.github {
  background-image: url("./assets/github.png");
}
.home-container > nav > i.qq {
  background-image: url("./assets/qq.png");
}
.home-container > nav > i.mail {
  background-image: url("./assets/mail.png");
}
.home-container > nav > i.resume {
  background-image: url("./assets/resume.png");
}
.home-container > p {
  width: 520px;
  color: rgba(0, 0, 0, 0.85);
  font-size: 18px;
}
@keyframes hvr-pulse-grow {
  10% {
    transform: translateX(3px) rotate(2deg);
  }
  20% {
    transform: translateX(-3px) rotate(-2deg);
  }
  30% {
    transform: translateX(3px) rotate(2deg);
  }
  40% {
    transform: translateX(-3px) rotate(-2deg);
  }
  50% {
    transform: translateX(2px) rotate(1deg);
  }
  60% {
    transform: translateX(-2px) rotate(-1deg);
  }
  70% {
    transform: translateX(2px) rotate(1deg);
  }
  80% {
    transform: translateX(-2px) rotate(-1deg);
  }
  90% {
    transform: translateX(1px) rotate(0);
  }
  100% {
    transform: translateX(-1px) rotate(0);
  }
}
